<template>
	<view>
		<view class="map-wrapper">
			<map style="width: 100%; height: 100%;" :latitude="userLocation.latitude"
				:longitude="userLocation.longitude" :markers="markers" :circles="circles" scale="14" min-scale="14"
				max-scale="20" show-location="true"></map>
		</view>
		<scroll-view scroll-y="true" v-if="nearby.length != 0" class="people-list-wrapper">
			<view class="user-info-wrapper" v-for="user, index in nearby">
				<view class="avatar-wrapper">
					<image src="../../static/avatar.png" class="avatar"></image>
				</view>
				<view class="username-wrapper">
					<navigator url="../matchedUserInfo/matchedUserInfo">
						<text class="username">{{user.username}}</text>
					</navigator>
				</view>
				<view class="distance-wrapper">
					<text class="distance">{{formatDistance(user.distance)}}</text>
				</view>

				<view class="user-status-wrapper">
					<text style="color: #6aff94;" v-if="user.status==1">在线</text>
					<text style="color: #DD524D;" v-else>离线</text>
				</view>
				<view class="tag-wrapper">
					<uni-tag :text="(user.gender==0?'♂  ':'♀  ')" :type="user.gender==0?'primary':'error'"
						size="small"></uni-tag>
				</view>

				<view class="description-wrapper">
					<text class="description">{{user.description}}</text>
				</view>
			</view>
		</scroll-view>
		<scroll-view v-else class="people-list-wrapper" scroll-y="true">
			<text class="hint">附近没有其他用户</text>
		</scroll-view>
	</view>
</template>

<script>
	import UniTag from '../../components/uni-tag/uni-tag.vue'
	export default {
		components: {
			UniTag,
		},
		data() {
			return {
				currentUserId: 1,
				host: 'http://1.14.43.96:8000',
				userLocation: {
					latitude: 30.579664,
					longitude: 103.984812,
				},
				/**
				 * 附近的用户
				 */
				nearby: [],
				markers: [],
				circles: [{
					latitude: 30.579664,
					longitude: 103.984812,
					radius: 1000,
					color: '#0099ff'
				}],
			}
		},
		mounted() {
			let _this = this
			// 进入页面后获取一次用户位置
			wx.getLocation({
				type: 'gcj02',
				isHighAccuracy: true,
				success: (res) => {
					_this.onGetLocationSuccess(res)
				}
			})
			
			// 每60s更新一次用户位置
			setInterval(function(){
				_this.updateLocation()
			}, 60000)
		},
		methods: {
			onGetLocationSuccess(res) {
				this.userLocation.latitude = res.latitude
				this.userLocation.longitude = res.longitude

				this.circles[0].latitude = this.userLocation.latitude
				this.circles[0].longitude = this.userLocation.longitude
				this.saveLocation()
				this.listNearbyPeople()
			},

			formatDistance(distance) {
				if (distance < 0.1) {
					return '<100m'
				} else {
					return Math.round(distance * 10) / 10 + 'km'
				}
			},
			listNearbyPeople() {
				let _this = this
				wx.request({
					url: this.host + '/location/nearby',
					method: 'GET',
					data: {
						latitude: this.userLocation.latitude,
						longitude: this.userLocation.longitude,
						limit: 20
					},
					success: (response) => {
						console.log(response)
						_this.nearby = []
						for (let i = 0; i < response.data.length; i++) {
							if (response.data[i].userId != _this.currentUserId) {
								_this.nearby.push(response.data[i])
							}
						}
						_this.addMarkers()

					}
				})
			},
			addMarkers() {
				let i = 0
				for (i = 0; i < this.nearby.length; i++) {
					let marker = {
						latitude: this.nearby[i].latitude,
						longitude: this.nearby[i].longitude,
						callout: {
							content: this.nearby[i].username + '\n' + this.formatDistance(this.nearby[i].distance)
						},
						iconPath: this.nearby[i].gender == 0 ? '../../static/marker1.png' : '../../static/marker2.png',
						width: 35,
						height: 40
					}
					this.markers.push(marker)
				}
			},
			saveLocation() {
				wx.request({
					url: this.host + '/location/?userId=' + this.currentUserId + '&latitude=' + this.userLocation
						.latitude + '&longitude=' + this.userLocation.longitude,
					method: 'POST',
					success: (res) => {

					}
				})
			},
			/**
			 * 定时更新用户位置
			 */
			updateLocation(){
				let _this = this
				wx.getLocation({
					type: 'gcj02',
					isHighAccuracy: true,
					success: (res) => {
						console.log("更新位置")
						_this.onGetLocationSuccess(res)
					}
				})
			}
		}
	}
</script>

<style scoped src="./nearbyPeople.css"></style>

<style>

</style>
